-----------------------------------*/
* {
  box-sizing: border-box;
}

.emergency-area {
  position: relative;
  background-image: url(/static/media/emergency-bg.5d3e5ff7.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
