.btn {
  display: inline-block;
  /* line-height: 26px; */
  font-weight: 400;
  /* border-radius: 5px; */
  text-align: center;
  transition: all 0.7s ease-in-out;
  position: relative;
  z-index: 1;
  /* border: none; */
  /* padding: 14px 28px 14px; */
  /* border-radius: 0; */
  color: #fff;
  overflow: hidden;
