.checkbox label:before {
  content: "";
  -webkit-appearance: none;
       appearance: none;
  background-color: transparent;
  border: 1px solid #2490EB;
  display: inline-block;
  width: 15px;
  height: 15px;
  position: absolute;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 5px;
