.checkbox input:checked + label:before {
  border-color: #2490EB;
}

.checkbox input:checked + label:after {
  content: "";
  display: block;
  position: absolute;
  top: 7px;
  left: 3px;
  width: 9px;
  height: 9px;
  border: none;
  background: #2490EB;
}

.checkbox.style2 input:checked + label:after {
