  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
}

.admin__page-section-content {
  margin-bottom: 20px;
}

.admin__page-section-item {
  margin-bottom: 15px;
}

.admin__page-section-item-title {
  display: flex;
  justify-content: space-between;
