/* Card container styling */
.dashboard-cards {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 20px;
  gap: 20px;
  margin-bottom: 40px;
}

/* Individual dashboard card styling */
.dashboard-card {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: calc(25% - 20px); /* Responsive design */
  min-width: 200px;
}

.card-title {
  font-size: 1.2rem;
