}

/* Recent orders section */
.recent-orders-section {
  margin-top: 40px;
}

.section-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
  border-bottom: 2px solid #e0e0e0;
  padding-bottom: 10px;
}

.recent-orders-list {
