  padding: 15px 20px;
  border-bottom: 1px solid #ddd;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: background-color 0.2s ease;
}

.recent-order-item:last-child {
  border-bottom: none;
}

.recent-order-item:hover {
  background-color: #f5f5f5;
}

.order-id {
  font-size: 1.1rem;
  font-weight: bold;
  color: #007bff;
}

.order-details {
  margin: 0;
  color: #555;
}

.order-details strong {
  color: #333;
}

