  }

  .recent-orders-list {
    margin: 0;
  }

  .recent-order-item {
    flex-direction: column;
    align-items: flex-start;
    grid-gap: 10px;
    gap: 10px;
  }

  .order-id {
    margin-bottom: 5px;
  }
