 .metismenu li{display: block;width: 100%;background: #f1b44c;border-bottom: 1px solid;color:#000;}
 .metismenu li a{color:#000 !important;}
 .metismenu li ul li{display: block;width: 100%;background:#404e67;border-bottom: 1px solid;color:#fff;}
 .metismenu li ul li a{color:#fff !important;}
 .metismenu li ul li:hover,
 .metismenu li ul li .focus,
 .metismenu li ul li .active {display: block;width: 100%;background:#f58633;border-bottom: 1px solid;}
#page-topbar {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 1002;
	background-color: #fff;
	box-shadow: 0 .75rem 1.5rem rgba(18, 38, 63, .03)
}

.navbar-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 0 auto;
	height: 70px;
	padding: 0 calc(24px / 2) 0 0
}

.navbar-header .dropdown.show .header-item {
	background-color: #f8f9fa
}

.navbar-brand-box {
	padding: 0 1.5rem;
	text-align: center;
	width: 230px
}

.logo {
	line-height: 70px
}

.logo .logo-sm {
	display: none
}

.logo-light {
	display: none
}

.app-search {
	padding: calc(32px / 2) 0
}

.app-search .form-control {
	border: none;
	height: 38px;
	padding-left: 40px;
	padding-right: 20px;
	background-color: #f3f3f9;
	box-shadow: none;
	border-radius: 30px
}

.app-search span {
	position: absolute;
	z-index: 10;
	font-size: 16px;
	line-height: 38px;
	left: 13px;
	top: 0;
	color: #74788d
}

.megamenu-list li {
	position: relative;
	padding: 5px 0
}

.megamenu-list li a {
	color: #495057
}

@media (max-width:992px) {
	.navbar-brand-box {
		width: auto
	}
	.logo span.logo-lg {
		display: none
	}
	.logo span.logo-sm {
		display: inline-block
	}
}

.page-content {
	padding: calc(70px + 24px) calc(24px / 2) 60px calc(24px / 2)
}

.header-item {
	height: 70px;
	box-shadow: none!important;
	color: #555b6d;
	border: 0;
	border-radius: 0
}

.header-item:hover {
	color: #555b6d
}

.header-profile-user {
	height: 36px;
	width: 36px;
	background-color: #f6f6f6;
	padding: 3px
}

.noti-icon i {
	font-size: 22px;
	color: #555b6d
}

.noti-icon .badge {
	position: absolute;
	top: 12px
}

.notification-item .media {
	padding: .75rem 1rem
}

.notification-item .media:hover {
	background-color: #f6f6f6
}

.dropdown-icon-item {
	display: block;
	border-radius: 3px;
	line-height: 34px;
	text-align: center;
	padding: 15px 0 9px;
	display: block;
	border: 1px solid transparent;
	color: #74788d
}

.dropdown-icon-item img {
	height: 24px
}

.dropdown-icon-item span {
	display: block;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap
}

.dropdown-icon-item:hover {
	border-color: #eff2f7
}

.fullscreen-enable [data-toggle=fullscreen] .bx-fullscreen::before {
	content: "\ea3f"
}

body[data-topbar=dark] #page-topbar {
	background-color: #34444c
}

body[data-topbar=dark] .navbar-header .dropdown.show .header-item {
	background-color: rgba(255, 255, 255, .05)
}

body[data-topbar=dark] .navbar-header .waves-effect .waves-ripple {
	background: rgba(255, 255, 255, .4)
}

body[data-topbar=dark] .header-item {
	color: #e9ecef
}

body[data-topbar=dark] .header-item:hover {
	color: #e9ecef
}

body[data-topbar=dark] .header-profile-user {
	background-color: rgba(255, 255, 255, .25)
}

body[data-topbar=dark] .noti-icon i {
	color: #e9ecef
}

body[data-topbar=dark] .logo-dark {
	display: none
}

body[data-topbar=dark] .logo-light {
	display: block
}

body[data-topbar=dark] .app-search .form-control {
	background-color: rgba(243, 243, 249, .07);
	color: #fff
}

body[data-topbar=dark] .app-search input.form-control::-webkit-input-placeholder,
body[data-topbar=dark] .app-search span {
	color: rgba(255, 255, 255, .5)
}

body[data-sidebar=dark] .navbar-brand-box {
	background-color: #536179;
	box-shadow: 0 1px 3px rgba(0, 0, 0, .2);
}

body[data-sidebar=dark] .logo-dark {
	display: none
}

body[data-sidebar=dark] .logo-light {
	display: block
}

@media (max-width:600px) {
	.navbar-header .dropdown {
		position: static
	}
	.navbar-header .dropdown .dropdown-menu {
		left: 10px!important;
		right: 10px!important
	}
}

@media (max-width:380px) {
	.navbar-brand-box {
		display: none
	}
}

body[data-layout=horizontal] .navbar-brand-box {
	width: auto
}

body[data-layout=horizontal] .page-content {
	margin-top: 70px;
	padding: calc(55px + 24px) calc(24px / 2) 60px calc(24px / 2)
}

@media (max-width:992px) {
	body[data-layout=horizontal] .page-content {
		margin-top: 15px
	}
}

.page-title-box {
	padding-bottom: 24px
}

.page-title-box .breadcrumb {
	background-color: transparent;
	padding: 0
}

.page-title-box h4 {
	text-transform: uppercase;
	font-weight: 600;
	font-size: 16px!important
}

.footer {
	bottom: 0;
	padding: 20px calc(24px / 2);
	position: absolute;
	right: 0;
	color: #74788d;
	left: 230px;
	height: 60px;
	background-color: #f2f2f5
}

@media (max-width:992px) {
	.footer {
		left: 0
	}
}

.vertical-collpsed .footer {
	left: 70px
}

body[data-layout=horizontal] .footer {
	left: 0!important
}

.right-bar {
	background-color: #fff;
	box-shadow: 0 0 24px 0 rgba(0, 0, 0, .06), 0 1px 0 0 rgba(0, 0, 0, .02);
	display: block;
	position: fixed;
	transition: all .2s ease-out;
	width: 280px;
	z-index: 9999;
	float: right!important;
	right: -290px;
	top: 0;
	bottom: 0
}

.right-bar .right-bar-toggle {
	background-color: #444c54;
	height: 24px;
	width: 24px;
	line-height: 24px;
	color: #eff2f7;
	text-align: center;
	border-radius: 50%
}

.right-bar .right-bar-toggle:hover {
	background-color: #4b545c
}

.rightbar-overlay {
	background-color: rgba(52, 58, 64, .55);
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	display: none;
	z-index: 9998;
	transition: all .2s ease-out
}

.right-bar-enabled .right-bar {
	right: 0
}

.right-bar-enabled .rightbar-overlay {
	display: block
}

@media (max-width:767.98px) {
	.right-bar {
		overflow: auto
	}
	.right-bar .slimscroll-menu {
		height: auto!important
	}
}

.metismenu {
	margin: 0
}

.metismenu li {
	display: block;
	width: 100%
}

.metismenu .mm-collapse {
	display: none
}

.metismenu .mm-collapse:not(.mm-show) {
	display: none
}

.metismenu .mm-collapse.mm-show {
	display: block
}

.metismenu .mm-collapsing {
	position: relative;
	height: 0;
	overflow: hidden;
	transition-timing-function: ease;
	transition-duration: .35s;
	transition-property: height, visibility
}

.vertical-menu {
	width: 230px;
	z-index: 1001;
	background: #fff;
	bottom: 0;
	margin-top: 0;
	position: fixed;
	top: 70px;
	box-shadow: 0 .75rem 1.5rem rgba(18, 38, 63, .03)
}

.sidebarparagraph{
	color: white;
